.home-taitel-list-all {
    height: 90vh;
    width: min(100%, 600);
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(100deg, #ecc9f1, rgb(147, 126, 206), #e5b5ebf6 30%);

}

.home-taitel-list {
    /* border: 1px solid black; */
    min-height: 70%;
    min-width: 50%;
    text-align: center;
}

.home-img-list {
    /* border: 1px solid black; */
    min-height: 70%;
    min-width: 50%;
    text-align: center;
}

.advac-img {
    border-radius: 50%;
    width: 550px;
    height: 400px;
    /* box-shadow: 0px 0px 5px white,
        0px 0px 10px white,
        0px 0px 20px white inset; */
}

.home-advac1 {
    color: #a23147;
    font-weight: 900;
    font-size: 20px;
}

.home-advac2 {
    color: white;
    text-shadow: 4px 4px 4px black;
    font-size: 50px;
}

.home-advac3 {
    font-weight: 900;
    color: #a23147;
    font-size: 20px;
}

.home-advac4 {
    font-weight: bolder;
}

.home-button {
    /* font-size: 30px; */
    padding: 10px;
    border-radius: 10px;
    background-color: #a23147;
    color: white;
    font-weight: bolder;
    text-decoration: none;

}

.home-button:hover {
    box-shadow: 0px 0px 5px white,
        0px 0px 10px white inset;
    color: black;
}


@media screen and (max-width:600px) {
    .home-taitel-list-all {
        display: grid;
    }

    .home-taitel-list {
        width: 100%;
    }


    .home-img-list {
        width: 100%;
    }

    .advac-img {
        width: 50%;
        height: 50%;
    }
}