.cartcontainar {
    height: 90vh;
    width: 40%;
    border: 1px solid black;
    overflow: auto;
}

.cartcontainar::-webkit-scrollbar {
    display: none;
}

.Header {
    display: flex;
    /* border: 2px solid black; */
    /* background-color: #31c5ff; */
    background: linear-gradient(250deg, hsla(294, 92%, 51%, 0.746), #2e79f9f9);
    /* background: linear-gradient(45deg, #00b5ff, #005eff); */
    justify-content: space-between;
    align-items: center;
}


.taitle {
    margin: 10px;
    padding: 10px;
}

.cart-icon {
    font-size: 30px;


}

.Bag-x {
    font-size: 20px;
    background-color: hsla(294, 76%, 61%, 0.746);
    font-weight: bolder;
}

.count {
    max-width: 35px;
    height: 20px;
    border: none;
    font-size: 30px;
    background-color: hsla(294, 76%, 61%, 0.746);

}

.qut {
    max-width: 35px;
    border: none;
    font-size: 30px;
    background-color: #31c5ff;

    /* background-color: #0762ffef; */
    /* background: linear-gradient(0deg, #00b5ff, #005eff); */

}

.cart-container {
    text-align: center;
}

.newcart {
    display: flex;
    margin-top: 10px;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    border-radius: 20px;
    justify-content: space-evenly;
    padding: 10px 0px 10px 0px;
    background-color: #31c5ff;

}

.cart-buttons {
    display: flex;
    justify-content: center;
}

/* .Addcart-listline {} */

.totalchag {
    font-weight: bolder;

}

.totalAll {
    border: 1px solid black;
    position: fixed;
    width: 100%;
    bottom: 0;
    background-color: rebeccapurple;
}

.total-price {
    text-align: left;

}

.cart-delete-button {
    border: none;
    /* background-color: #00b5ff; */
    /* background: linear-gradient(10deg, #00b5ff, #005eff); */
    background-color: #31c5ff;

    >i {
        font-size: 20px;
        color: red;
    }
}

.cart-newimg {
    height: 50px;
    /* width: 50px; */
}


@media screen and (max-width:600px) {
    .cartcontainar {
        display: grid;
        width: 800px;

    }


}